/*=================================
=            UNIVERSAL            =
=================================*/

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

h1, h2, h3 {
	margin: 0;
}

html {
height: 100%;
}

body {
	// background-color: $bodybg; 
	// color: $bodytext;
	min-height: 100%;

  background: rgba(0,179,161,1);
  background: -moz-linear-gradient(left, rgba(0,179,161,1) 0%, rgba(0,105,112,1) 29%, rgba(0,105,112,1) 70%, rgba(0,179,161,1) 100%);
  background: -webkit-gradient(left top, right top, color-stop(0%, rgba(0,179,161,1)), color-stop(29%, rgba(0,105,112,1)), color-stop(70%, rgba(0,105,112,1)), color-stop(100%, rgba(0,179,161,1)));
  background: -webkit-linear-gradient(left, rgba(0,179,161,1) 0%, rgba(0,105,112,1) 29%, rgba(0,105,112,1) 70%, rgba(0,179,161,1) 100%);
  background: -o-linear-gradient(left, rgba(0,179,161,1) 0%, rgba(0,105,112,1) 29%, rgba(0,105,112,1) 70%, rgba(0,179,161,1) 100%);
  background: -ms-linear-gradient(left, rgba(0,179,161,1) 0%, rgba(0,105,112,1) 29%, rgba(0,105,112,1) 70%, rgba(0,179,161,1) 100%);
  background: linear-gradient(to right, rgba(0,179,161,1) 0%, rgba(0,105,112,1) 29%, rgba(0,105,112,1) 70%, rgba(0,179,161,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00b3a1', endColorstr='#00b3a1', GradientType=1 );
}    

ul {
	list-style-type: none; 
}


