    align-self: center;
  } 
}

.content {
  margin-top: 30px;

}


/*=========================================
=            MOBILE NAVIGATION            =
=========================================*/

#sticky-nav {
  position: fixed;
  top: 0;
  width: 100%;
 // margin-bottom: 20px;
  z-index: 100;
}

.mobile-nav {
  position: relative; //needed to make drop-down hover
  width: 100%; //needed to make drop-down hover
  margin-bottom: 20px;

  @include largetablet {
    display: none;
  }

  /*----------  Hamburger Menu  ----------*/
  .hamburger {
    width: 100%; 
    padding: 12px;
    background-color: $navbg;
    cursor: pointer;
    z-index: 100;
    position: relative;

    div {
      border-bottom: $hamburger-style;
      width: 35px;
      margin-bottom: 6px;
      margin-left: auto;
      margin-right: auto; 
    }
    div:last-child {
      margin-bottom: 0;  
    }
  }

  /*----------  Nav list  ----------*/
  ul {
    z-index: 10; //needed to make drop-down hover
    position: absolute; //needed to make drop-down hover
    width: 100%; //needed to make drop-down hover
    padding-top: 31px; // WAS 51 
    transition: 1s; 

    li { 
      width: 100%;
      @extend %navcolor;

      a {
        @extend %navstyle;
      }
    }
  }
  ul:last-child {
    border-bottom: $nav-border;
  }
}

 



