/*----------  PORTFOLIO  ----------*/

.overlay {
   width:220px;
   height:220px;
   display:none; 
   position: absolute;
   background-color: black;
   top: 0;
   opacity: 0.75;
   z-index: 100;
   padding: 14px 8px 0;
   border-radius: 5px;
  p {
    text-align: center;
    padding: 5px;
  }
} //end overlay

.portfolio-wrap {
  flex-direction: column;
  @include largetablet {
    flex-direction: row; 
    flex-wrap: wrap;
  }
} //end portfolio wrap

#portfolio {
  padding: 80px 5px 0; 
  flex-direction: column;

   @include largetablet {
      flex-direction: row; 
      flex-wrap: wrap;
      justify-content: center;
      min-width: 95%; 
  }

  p {
      margin-bottom: 25px;
  }

  h1 {
    margin-bottom: 30px;  
  } 

 .thumb { 
    @extend %widgetborders;
    background-color: $widget-bg; 
    margin: 20px 0px; 
    padding: 10px 5px;


    @include landscapephone {
      padding: 10px;
      max-width: 75%;
      margin: 20px auto;
    } 
    // @include largetablet {
    //   max-width: 75%;
    // }
    @include largetablet {
      min-width: 455px; 
      max-width: 80%;
      padding: 0; 
    } 
  } //end thumb

  p {
      margin-bottom: 10px;
  }

  .thumb-container {
    border-radius: 5px; 
    margin-bottom: 12px;

    img {
      width: 85%; 
      margin-bottom: 15px;
      border: 5px solid #000;
      @include largetablet {
        //border-radius: 5px;
        width: 220px;
        height: 220px;   
      }
    } 
  }

  .skill-wrap {
    width: 85%;
    margin-bottom: 10px;
    h3 {
      margin: 10px 0 10px; 
    }
    .skills {
        @include margins(10px, 10px);
        li {  
            background-color: $skillsbg;
            border-radius: 5px; 
            border: $skills-border;
            padding: 5px;
            margin: 6px;
        }
    }
}


  .hidelarge {
    margin: 0 auto 20px;
    max-width: 90%; 
  }

  h2 {
    margin: 25px 0 25px;
  }



  @include largetablet {

    .thumb-container {   //test
      position: relative;
    }
    .thumb-container:hover .overlay {
      display: flex; 
      justify-content: center;
      align-items: center;
      z-index: 100;
    } 
    .hidelarge {
      display: none; 
    }
  }

} //end portfolio



/*----------  CONTACT WIDGET  ----------*/
#contact {
  padding-top: 80px;

  @include largephone {
    max-width: 85%;
    margin: 0 auto;
  }

  h1 {
    margin-bottom: 30px; 
  } 
  .intro {
    margin: 0 auto 20px;
    max-width: 90%; 
  }
.contactbox {
    @extend %widgetborders;
    background-color: $widget-bg; 
    margin: 20px 10px; 
    padding: 10px 5px; 
    width: 95%; 
 
    @include landscapephone {
      max-width: 500px;  
    }

    ul {
      padding-left: 30px;
      padding-right: 3px;

      @include largetablet { 
        padding-right: 20px;
      }

      li { 
        list-style-type: circle;
        padding: 5px 0;

        a {
          color: $genlink;
        }
        a:hover {
          color: $genlinkhover;
        }
      }
    }
    h2 { //was h1
      margin-bottom: 8px; 

      @include largetablet {
        margin-right: 20px;

      }
    }
    .split {
      .l-split {
        @include largetablet {
          margin-right: 20px;
        }
      }
    } 

    .leftalign {
      @include largetablet {
        width: 100px;

        text-align: center;
      }
    }
  }

  .socialbox p {
    margin: 5px 0;
  }

  .socialcontact a,
  .githubcontact a {
      border-radius: 10px; 
      width: 50px;
      height: 50px;
      color: $contactbuttons;
      text-align: center;
      line-height: 50px;
     -webkit-transform: translate3d(0, 0, 0);
     transform: translate3d(0, 0, 0);
     -webkit-transition: -webkit-transform ease-out 200ms;
     transition: -webkit-transform ease-out 200ms;
     transition: transform ease-out 200ms;
     transition: transform ease-out 200ms, -webkit-transform ease-out 200ms;
     margin: 10px;
  }

  div:nth-of-type(3) {
    margin-bottom: 70px;
  }

  // ICON COLORS //

  .green {
     background-color: $linkedin;
  }

  .lightblue {
     background-color: $twitter;
  }

  .red {
     background-color: $google;
  } 

  .purple {
     background-color: $github;
  }

  .green,
  .lightblue,
  .red,
  .purple {
     box-shadow: 3px 3px 0 0 rgba(0, 0, 0, 0.14);
     text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.12);
  }

  .green:hover,
  .lightblue:hover,
  .red:hover,
  .purple:hover {
     color: $black; 
     text-shadow: none;
  }
}


/*----------  TREEHOUSE WIDGET  ----------*/

#modal {
  background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.7); /* Black w/ opacity */
  display: none;
  color: #fff; 
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 999;
  position: fixed;
  left: 0;
  top: 0;
    -webkit-transition: display 1.5s;
    transition: display 1.5s;
}
#treehouse {
  background-color: $dkgray;
  margin: 15px auto 0;
  padding: 15px;
  border: 1px solid #888;
  width: 80%;
  max-width: 400px;
  border-radius: 15px;
}

.team-treehouse {
  .tPoints {
    display: none;
  }
  .pieChart {
      .pie {
        display: none;
      }
      .legend {
        display: none;
        text-align: center;
      }
  }
}

