// properties
$color-background: #000; //f9f9f9
$color-primary: #222222;
$size-circle: 10.5em; //12.5
$size-thickness: 2.5em;
$size-inner: $size-circle - $size-thickness * 2;


// clearfix
.group:after {content:"";display:table;clear:both;}

// general

  .team-treehouse {
	
	margin-top: 15px;
	margin-bottom: 10px;
    h1, h2 {
      text-align: center;
    } 
    h1 {
    	font-size: 2.2rem;
    }
    h2 {
      font-weight: normal;
 	  font-size: 1.2rem; 
 	  margin-top: 15px;
 	  margin-bottom: 15px;
 	
    }
  }



// pie chart
.pieChart {
  width: $size-circle + 5em; //will need to make this bigger at larger screens. originally was size-circle +13em
  margin: 0 auto;
  padding-top: 1em;
}

// pie
.pie {
  height: $size-circle;
  width: $size-circle;
  position: relative;
  float:left;
  margin-bottom: 25px; 

  &::before {
    content: "";
    display: block;
    position: absolute;
    z-index: 1;
    width: $size-inner;
    height: $size-inner;
    background: $color-background;
    border-radius: 50%;
    top: $size-thickness;
    left: $size-thickness;
  }
}

.slice {
  position: absolute;
  width: $size-circle;
  height: $size-circle;
  clip: rect(0px, $size-circle, $size-circle, $size-circle/2);
  animation: bake-pie 1s;

  span {
    display: block;
    position: absolute;
    top: 0;
    left: 0; 
    background-color: black;
    width: $size-circle;
    height: $size-circle;
    border-radius: 50%;
    clip: rect(0px, $size-circle, $size-circle, $size-circle/2);
  }
}

// legend
.legend {
  width: 13em;
  padding-left: 0.5em; 
  padding-right: 0.6em; 
  float: left; 
  
  ul {
    list-style-type: none;
    list-style-position: inside;
    font-size: 0.8em;
  }
  li {
    height: 1.25em;
    margin-bottom: 0.7em;
    padding-left: 0.5em;
    border-left: 1.25em solid black;
  }
  em {
    font-style: normal;
  }
  span {
    float: right;
    font-weight: bold;
  } 
}

  #close {
    margin: 10px 0 0 0; 
    text-align: right;
    transition: color 0.5s ease-in;
  } 
  #close:hover {
    cursor: pointer;
    color: $navtxthover;
  }

